import React from 'react';
import { withAlert } from 'react-alert'

class ContactForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      _replyto: '',
      phone: '',
      message: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleSubmit(event) {
    const { name, email, message } = this.state;
    if(name.trim() === '' || email.trim() === '' || message.trim() === '') {
      this.props.alert.show('Whoops! Name, Email and Message are required.');
      event.preventDefault();
    }
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit} action="https://formspree.io/eces1010@gmail.com" method="POST">
        <input type='text' name='name' placeholder='Name' value={this.state.name} onChange={this.handleChange} />
        <input type='text' name='_replyto' placeholder='Email' value={this.state.email} onChange={this.handleChange} />
        <input type='text' name='phone' placeholder='Phone' value={this.state.phone} onChange={this.handleChange} />
        <textarea name='message' placeholder='How can I help you?' value={this.state.message} onChange={this.handleChange} />
        <button type='submit' className='btn'>Send</button>
      </form>
    )
  }
}

export default withAlert(ContactForm)



