import React from 'react'
import Avatar from 'react-avatar';
import ScrollableAnchor from 'react-scrollable-anchor'
import Header from './header'
import Page from './page';
import Section from './section';
import ContactForm from './contact-form';
import { Provider as AlertProvider } from 'react-alert'
import AlertTemplate from 'react-alert-template-basic'

const alertOptions = {
    offset: '14px',
    position: 'top right',
    timeout: 5000,
	transition: 'fade'
};

export default class Home extends React.Component {
	render() {
		return(
			<div>
				<Header />
				<Page>
					<ScrollableAnchor id={'top'}>
						<Section styles='hero'>
							<div className='background-image' style={{backgroundImage: `url(${process.env.PUBLIC_URL}/img/wallpaper-2819550.jpg)`}}></div>
							<div>
								<Avatar src={`${process.env.PUBLIC_URL}/img/me2.png`} size={200} round={true}/>
							</div>
							<h1>Michelle Chung</h1>
						</Section>
					</ScrollableAnchor>	
					
					<ScrollableAnchor id={'background'}>
            			<Section styles='our-work'>
							<h3 className='title'>My Background</h3>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
							<p>.................................................................</p>
						</Section>
          			</ScrollableAnchor>
				
			        <ScrollableAnchor id={'email-me'}>
            			<Section styles='contact'>
          					<h3 className='title'>Contact Me!</h3>
							<AlertProvider template={AlertTemplate} {...alertOptions}>
          						<ContactForm />
							</AlertProvider>
              				<p></p>
          				</Section>
					</ScrollableAnchor>
				</Page>
			</div>
		)
	}
}



